import React, { useEffect, useState } from 'react';
import {
  IonApp,
  IonButton,
  IonCol,
  IonRow,
  IonLoading,
  IonItem,
  IonLabel,
  IonSelect,
  IonSelectOption,
} from '@ionic/react';
import './allStyles'

import Layout from './components/Layout';

import axios from 'axios';
import Card from './components/Card';

interface IData {
  itemsImage: string,
  itemsName: string,
  lowSellPrice: string,
  itemsPrice: string,
  itemPriceSave: string,
  itemsLink: string,
  itemsRating: string,
}

enum Category {
  laptop = "https://www.winwin.rs/laptop-i-tablet-racunari/laptop-notebook-racunari.html",
  desktop = "https://www.winwin.rs/racunari-i-komponente/desktop-racunari/desktop-racunari-bez-os.html",
  smart = "https://www.winwin.rs/mobilni-i-fiksni-telefoni/mobilni-telefoni/smart-mobilni-telefoni.html",
  maticne = "https://www.winwin.rs/racunari-i-komponente/racunarske-komponente/maticne-ploce.html",
  procesori = "https://www.winwin.rs/racunari-i-komponente/racunarske-komponente/procesori.html",
  monitori = "https://www.winwin.rs/pc-periferije-monitori/monitori-870/monitori.html",
}


const App: React.FC = () => {
  const defaultPosts: IData[] = [];
  const [posts, setPosts]: [IData[], (posts: IData[]) => void] = useState(defaultPosts);
  const [category, setCategory] = useState<string>(Category.laptop)
  const [pageNumber, setPageNumber] = useState<number>(1)
  const [loading, setLoading]: [boolean, (loading: boolean) => void] = useState<boolean>(true);
  const [error, setError]: [string, (error: string) => void] = useState<string>('');

  useEffect(() => {
    const fetch = () => {
      axios.post<IData[]>("https://webscrape-ionic.herokuapp.com/api", {
        pageNumber,
        category
      }).then(({ data }) => {
        setPosts(data);
        setLoading(false);
      }).catch(ex => {
        const error = ex.response.status === 404 ? "Resource not found" : "Unexpected error occured"
        setError(error)
        setLoading(false);
      })
    }
    fetch()
  }, [pageNumber, loading, category])

  const nextPage = () => {
    setPageNumber(pageNumber + 1);
    window.scrollTo(0, 0)

  }
  const previousPage = () => {
    if (pageNumber === 1) {
      return
    }
    setPageNumber(pageNumber - 1)
    window.scrollTo(0, 0)
  }


  return (
    <IonApp>
      <Layout>
        {loading ? (
          <IonLoading
            isOpen={loading}
            message={'Molimo vas sacekajte'} />
        ) : null}
        {error && <p className="error">{error}</p>}
        <IonItem>
          <IonLabel>Kategorija</IonLabel>
          <IonSelect value={category} placeholder="Odaberite" onIonChange={(e) => {
            setCategory(e.detail.value)
            setPageNumber(1)
          }}>
            <IonSelectOption value={Category.laptop}>Laptop</IonSelectOption>
            <IonSelectOption value={Category.desktop}>Desktop</IonSelectOption>
            <IonSelectOption value={Category.maticne}>Maticne Ploce</IonSelectOption>
            <IonSelectOption value={Category.procesori}>Procesori</IonSelectOption>
            <IonSelectOption value={Category.smart}>Mobilni Telefoni</IonSelectOption>
            <IonSelectOption value={Category.monitori}>Monitori</IonSelectOption>
          </IonSelect>
        </IonItem>

        <IonRow>
          <IonCol className="ion-justify-self-center">
            <IonButton color="dark" onClick={previousPage}>Nazad</IonButton>
            <IonButton color="dark" onClick={nextPage}>Dalje</IonButton>
          </IonCol>
        </IonRow>
        <IonRow className="ion-justify-content-center">
          <IonCol>
            {posts.map((post, id) => (
              <Card
                key={id}
                itemsName={post.itemsName}
                itemsImage={post.itemsImage}
                itemsLink={post.itemsLink}
                itemsPrice={post.itemsPrice}
                itemPriceSave={post.itemPriceSave}
                itemsLowSellPrice={post.lowSellPrice}
                itemsRating={post.itemsRating} />
            ))}
          </IonCol>
        </IonRow>
        <IonButton color="dark" onClick={previousPage}>Nazad</IonButton>
        <IonButton color="dark" onClick={nextPage}>Dalje</IonButton>
        {error && <p className="error">{error}</p>}
      </Layout>
    </IonApp >
  )
};

export default App;
